.table-page {
    height: 100vh;
    display: flex;
    flex-direction: column;
    padding-left: 1rem;
    padding-right: 1rem;
    background-color: #f0f0f0;
}

.table-page__back-button {
    margin-top: 1rem;
}
.table-page__back-button button{
    padding: .5rem 1rem;
    font-size: 1.25rem;
    background-color: transparent;
    border: none;
    border-radius: 1.5rem;
    cursor: pointer;
    font-weight: 500;
    display: flex;
    align-items: end;
    transition: background-color 0.3s ease;
}
.table-page__back-button button:hover{
    background-color: #d9d9d9;
}
.table-page__back-button button .bi{
    margin-right: 8px;
    margin-bottom: 1px;
}

.table-page__title {
    font-size: 5rem;
    font-weight: 300;
    margin: 0;
}

.selected-courses {
    padding: 1rem;
    background-color: #d9d9d9;
    border-radius: 2rem;
    cursor: not-allowed;
}

.selected-courses.active {
    background-color: #0ae;
    color: white;
    cursor: pointer;
    transition: background-color 0.3s ease;
}
.selected-courses.active:hover {
    background-color: #008bbf;
    color: white;
    cursor: pointer;
}
.selected-courses.active a{
    color: inherit !important;
    text-decoration: none;
    font-weight: 500;
}

.selected-courses.inactive {
    color: #aaa;
}

.selected-courses__container {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    padding: 0 1rem;
}

@media (max-width: 991.98px) {
    .table-page__title {
        font-size: 4rem;
    }
    .table-page {
        padding-left: .75rem;
        padding-right: .75rem;
    }
    .selected-courses {
        padding: .5rem;
    }
}

@media (min-width: 768px) and (max-width: 991.98px) and (max-height: 575.98px) {
    .table-page__title {
        font-size: 2.5rem;
        margin-left: 1rem;
    }
}