:root {
    --stamp-button-size: 125px;
    --stamp-button-font: 1.25rem;
}

.stamp-button {
    position: relative;
    padding: 12px 24px;
    color: white;
    width: var(--stamp-button-size);
    height: var(--stamp-button-size);
    background-color: transparent;
    border: none;
    border-radius: 9999px;
    cursor: pointer;
    overflow: hidden;
    transition: transform 0.3s ease;
    margin: 0;
}

.stamp-button:hover {
    transform: scale(1.05);
    background-color: transparent
}

.stamp-button:active {
    transform: scale(0.95);
}

.stamp-button__background {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 0;
    mask-image: var(--stamp-svg);
    mask-size: contain;
    mask-repeat: no-repeat;
    mask-position: center;
    background-color: var(--stamp-color);
}

.stamp-button__background.regular {
    animation: rotate 50s linear infinite;
}

.stamp-button__background.slow {
    animation: rotate 100s linear infinite;
}

.stamp-button__text {
    position: relative;
    z-index: 1;
    font-family: 'brandon-grotesque';
    font-size: var(--stamp-button-font);
    line-height: 0.9;
}

.stamp-button__text.tilt {
    transform: rotate(-10deg)
}

@keyframes rotate {
    from {
        transform: rotate(0deg);
    }

    to {
        transform: rotate(360deg);
    }
}