.opacity-75 {
    opacity: .75;
}
.animated-button__image {
    max-width: 256px;
    width: 100%;
}

@media (max-width: 991.98px) {
    .animated-button__image {
        max-width: 128px;
        width: 100%;
    }
}

@media (min-width: 768px) and (max-width: 991.98px) and (min-height: 575.98px) {
    .animated-button__image {
        max-width: 192px;
        width: 100%;
    }
}

/* .animated-button__label {
