} */

.floating {
    animation: float 5s ease-in-out infinite;
}

@keyframes float {
    0% {
        transform: translateY(0);
    }
    50% {
        transform: translateY(-10px);
    }
    100% {
        transform: translateY(0);
    }
}

.grow {
    transition: all 0.3s ease;
}

.grow:hover {
    transform: scale(1.05);
}

.grow:active {
    transform: scale(0.95);
}