.filterable-table {
    display: flex;
    flex-direction: column;
    flex-grow: 1;
    font-family: -apple-system, BlinkMacSystemFont,
    'Segoe UI Adjusted', 'Segoe UI', 'Liberation Sans', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
}

.filterable-table input {
    border: 0;
    border-bottom: #d0d0d0 6px dotted;
    border-radius: 2rem 2rem 0 0;
    margin: 0;
    padding: 1rem 2rem;
    font-size: .9rem;
}

.filterable-table .table-container {
    overflow: auto;
    flex-grow: 1;
    height: 50vh;
}

.filterable-table .table {
    width: 100%;
    border-collapse: separate;
    border-spacing: 0;
    background-color: #fafafa;
}

.filterable-table .table__header {
    background-color: white;
}

.filterable-table .table__header th{
    padding: 1rem 1rem 1rem 0;
    font-weight: 400;
}

.filterable-table .table__header th.table__heading-small {
    font-size: .9rem;
    font-weight: 500;
    line-height: 0.9rem;
}

.filterable-table .table__header th.table__heading {
    font-size: 1.75rem;
}

.filterable-table tbody td:not(.accordion) {
    padding: 1rem .5rem;
}
.filterable-table tbody td:not(.accordion)[col*="1"], .filterable-table tbody td:not(.accordion)[col*="3"] {
    text-align: right;
    font-size: 1.5rem;
    font-weight: 600;
    font-family: "brandon-grotesque", -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
    font-variant-numeric: tabular-nums;
}
/* Checkbox */
input[type="checkbox"] {
    display: none;
}

/* Custom checkbox */
input[type="checkbox"] + label {
    cursor: pointer;
}
input[type="checkbox"][disabled=""] + label {
	cursor: not-allowed;
}

/* Create the custom checkbox */
.filterable-table td:not(.accordion)[col*="0"], .filterable-table th[col*="0"] {
    padding: 1rem;
}
.filterable-table tbody td:not(.accordion)[col*="0"] {
    position: relative;
}

input[type="checkbox"] + label:before {
    --checkbox-size: 40px;
    content: "";
    position: absolute;
    left: calc(50% - (var(--checkbox-size)/2));
    top: calc(50% - (var(--checkbox-size)/2));
    width: var(--checkbox-size);
    height: var(--checkbox-size);
    border: none;
    border-radius: 8px;
    background-color: #D9D9D9; /* background color */
    transition: background-color 0.3s ease;
}

/* Checked state */
input[type="checkbox"]:checked + label:before {
    background-color: #0ae; /* custom checkbox color when checked */
}

/* Checkmark inside checked state */
input[type="checkbox"]:checked + label:after {
    content: "\f633"; /* Unicode character for checkmark */
    font-family: bootstrap-icons !important;
    vertical-align: -.125em;
    line-height: 1;
    font-size: 1rem;
    position: absolute;
    left: calc(50% - 8px);
    top: calc(50% - 8px);
    color: #fff; /* color of the checkmark */
}


.filterable-table .table tbody tr[style*="background-color: #ccc"] {
    opacity: 0.5;
}

.filterable-table tbody td:not(.accordion)[col*="7"] {
    display: flex;
    justify-content: end;
    align-items: center;
    text-align: right;
    padding-right: 0;
    font-size: .85rem;
    font-weight: 500;
}

.accordion {
    padding: 0;
}

.accordion-button {
    margin: 0 .75rem 0 1rem;
    padding: 0;
    width: 32px;
    height: 32px;
    background-color: transparent;
    border: none;
    border-radius: 32px;
    cursor: pointer;
    transition: background-color 0.3s ease, transform 150ms ease;
    color: #000!important;
}

.accordion-button .bi {
    font-size: 1.5rem;
}

.accordion-button:hover {
    background-color: #e0e0e0;
}

.accordion-button.active {
    /* background-color: #d0d0d0; */
    transform: rotate(180deg);
}

.accordion-row {
    transition: background-color 150ms ease;
}

.accordion-content {
    max-height: 0;
    overflow: hidden;
    transition: max-height 150ms ease-out;
    background-color: #f9f9f9;
    border: 0;
}

.accordion-content>div {
    padding: 15px;
}

@media (max-width: 991.98px) {
    .filterable-table input {
        font-size: .75rem;
    }
    .filterable-table .table__header th.table__heading {
        font-size: 1rem;
        font-weight: 500;
    }
    .filterable-table .table__header th.table__heading-small {
        font-weight: 400;
    }
    .filterable-table td:not(.accordion)[col*="0"], .filterable-table th[col*="0"] {
        padding: .5rem;
    }
    .filterable-table tbody td:not(.accordion)[col*="1"], .filterable-table tbody td:not(.accordion)[col*="3"] {
        font-size: .75rem;
        padding-right: .5rem;
        font-weight: 600;
    }
    .filterable-table tbody td:not(.accordion) {
        padding: 1rem .25rem;
        line-height: 1rem;
        font-size: .75rem;
    }
    input[type="checkbox"] + label::before {
        --checkbox-size: 30px;
        border-radius: 8px;
    }
    .filterable-table tbody td:not(.accordion)[col*="7"] {
        font-size: .6rem;
    }
    .table-page__back-button {
        margin-top: 0;
    }

    
}

/* New styles for mobile adaptation */
@media (max-width: 576px) {
    .filterable-table input {
        font-size: 0.75rem;
    }

    .filterable-table .table__header th.table__heading {
        font-size: 0.9rem;
        font-weight: 500;
    }

    .filterable-table .table__header th.table__heading-small {
        font-weight: 400;
    }

    .filterable-table td:not(.accordion)[col*="0"],
    .filterable-table th[col*="0"] {
        padding: 0.5rem;
    }

    .filterable-table tbody td:not(.accordion)[col*="1"],
    .filterable-table tbody td:not(.accordion)[col*="3"] {
        font-size: 0.75rem;
        padding-right: 0.5rem;
        font-weight: 600;
    }

    .filterable-table tbody td:not(.accordion) {
        padding: 0.75rem 0.25rem;
        line-height: 1rem;
        font-size: 0.75rem;
    }

    input[type="checkbox"] + label::before {
        --checkbox-size: 30px;
        border-radius: 8px;
    }

    .filterable-table tbody td:not(.accordion)[col*="7"] {
        justify-content: center;
    }

    .accordion-button .bi {
        font-size: 1.25rem;
    }

    .accordion-button {
        display: flex;
        justify-content: center;
        align-items: center;
        margin: 0;
        width: 32px;
        height: 32px;
    }

    .accordion-content > div {
        padding: 10px;
        font-size: 0.75rem;
    }

    .accordion-content > p:first-child {
        font-weight: bold;
        margin-bottom: 10px;
    }
}

@media (min-width: 768px) and (max-width: 991.98px) {
    .filterable-table .table__header th {
        padding: .5rem 1rem .5rem 0;
        font-weight: 400;
    }
    .filterable-table .table__header th[col*="0"] {
        padding: .5rem 1rem;
    }
    .accordion-content > div {
        font-size: 0.75rem;
    }
    .filterable-table input {
        padding: .75rem 2rem;
    }
}