.cover-logo {
    height: 40px;
}

.cover-lettering__container {
    max-width: 75vw;
    text-align: center;
    margin: 0 auto;
    flex-grow: 1;
    justify-content: space-evenly;
    display: flex;
    flex-direction: column;
    position: relative;
}

.cover-lettering__title {
    font-size: 9rem;
    line-height: .9;
    margin: 0;
    z-index: 900;
}

.cover-lettering__button {
    position: absolute;
    bottom: 2rem;
    /* 37.5vw: centers button, 62.5px removes its own width, 315px offsets from the center to the left */
    left: calc(37.5vw - 62.5px - 325px);
    z-index: 0;
}

.cover-lettering {
    position: relative;
}

.cover-lettering__branding {
    font-weight: bold;
    line-height: 1.25;
}

.cover-action {
    font-size: 1.25rem;
    padding: 0;
    margin: 0;
}

.cover-action h3 {
    padding: 0;
    margin: 0;
    font-weight: 500;
}

.cover-action h3 span {
    font-weight: 300;
}

.cover-lettering__start {
    cursor: pointer;
}

@media (max-width: 767.98px) {
    .cover-lettering__title {
        font-size: 3rem;
    }

    .cover-lettering__container {
        justify-content: space-around;
        max-width: none;
    }

    .cover-lettering__button {
        left: calc(50vw - 62.5px - 2.25rem);
        margin-top: 1.5rem;
        bottom: auto;
    }

    .cover-lettering__branding {
        line-height: 1.1;
        font-size: 5rem;
    }

    .cover-action {
        margin-top: 3rem;
    }
}

@media (min-width: 768px) and (max-width: 991.98px) {
    .cover-lettering__title {
        font-size: 6rem;
    }
    .cover-lettering__button {
        left: calc(37.5vw - 62.5px);
        margin-top: 1.5rem;
        bottom: auto;
    }
    .cover-lettering__container {
        justify-content: space-around;
    }
}

@media (min-width: 768px) and (max-width: 991.98px) and (max-height: 575.98px) {
    .cover-logo {
        height: 30px;
        margin-bottom: 1rem;
    }
    .cover-lettering__title {
        font-size: 3.5rem;
    }
    .cover-lettering__button {
        left: calc(30vw - 62.5px - 12.5rem);
        top: 4rem;
        bottom: auto;
    }
    .cover-lettering__container {
        justify-content: space-between;
        max-width: 70vw;
    }
    /* :root {
        --stamp-button-size: 100px!important;
        --stamp-button-font: 1rem!important;
    } */
}