ul.sortable-list {
    list-style-type: none;
    padding: 0;
}

li.sortable-item {
    /* Border */
    border: 4px dotted transparent;
    border-bottom: 2px solid rgba(0, 171, 238, 0.1);

    /* DnD behavior */
    cursor: grab;
    user-select: none;         /*  Disable text selection: modern browsers */
    -webkit-user-select: none; /* Disable text selection: Safari and iOS */
    touch-action: none;        /* Prevent mobile default touch behavior */
    -webkit-user-drag: none;   /* Disable dragging on iOS */

    /* Font */
    font-size: 1.25rem;
    font-weight: 500;

    /* Spacing */
    display: flex;
    align-items: center;
    padding: 1rem 1.5rem;

    /* Scrolling */
    overflow: auto;
}

li.sortable-item .item-grip {
    display: flex;
    align-items: center;
    margin-left: -8px; /* 8px accounts for the icon spacing */
    margin-right: calc(1.5rem - 8px);
    color: #acacac;
}
li.sortable-item .item-number {
    margin-right: 1.5rem;
    color: #0ae;
    font-size: .9rem;
    font-weight: 600;
    text-transform: uppercase;
}
li.sortable-item .item-number sup{
    text-transform: lowercase;
}
li.sortable-item .item-info {
    display: flex;
    align-items: center;
    flex-grow: 1;
    line-height: 1;
}
li.sortable-item .course-info {
    flex-grow: 1
}

li.sortable-item:active {
    cursor: grabbing;
}

li.sortable-item.sorting:not(.dragging) {
    opacity: 0.5;
}

li.sortable-item.dragging {
    background-color: #ececec;
    border: 4px dotted #d5d5d5;
    opacity: 1;
}


li.sortable-item .course-code {
    color: #0ae;
    font-weight: 400;
    font-size: 1.75rem;
    margin-right: .5rem;
}
li.sortable-item .course-period, li.sortable-item .course-location {
    font-weight: 400;
    font-size: 1rem;
}
li.sortable-item span:not(:last-child) {
    margin-right: .5rem;
}
@media (max-width: 767.98px) {
    li.sortable-item .item-number {
        font-size: .75rem;
    }
    li.sortable-item {
        padding: .5rem 1rem;
    }
}