.areas__container {
    text-align: center;
    display: flex;
    flex-direction: column;
    flex-grow: 1;
    justify-content: space-around;
}

.areas__title {
    font-size: 5rem;
    font-weight: 300;
    line-height: .9;
}

/* .areas__buttons {
	display: flex;
	justify-content: space-evenly;
    flex-wrap: wrap;
} */

.areas__buttons a{
    color: white;
    text-decoration: none;
    font-size: 2rem;
    font-weight: 500;
    line-height: 1.1;
    font-family: 'brandon-grotesque';
}

/* .areas__button {
    width: 33.333%
} */

.areas__more button{
    background-color: transparent;
    border: none;
    border-radius: 1.5rem;
    color: white;
    font-weight: 500;
    font-size: 1.25rem;
    margin-top: 1rem;
    font-family: 'brandon-grotesque';
    padding: .5rem 1rem;
    transition: background-color 0.3s ease;
}

.areas__more button:hover{
    background-color: #008bbf;
    cursor: pointer;
}

@media (max-width: 991.98px) {
    .areas__title {
        font-size: 2.75rem;
    }
    /* .areas__button {
        width: 100%;
        flex: 0 0 auto;
    } */
    .areas__buttons a{
        font-size: 1.25rem;
    }
    .areas__button:not(:last-child) {
        margin-bottom: 1.5rem;
    }
    .areas__container {
        justify-content: space-between;
    }
    .areas__more button{
        padding-bottom: 0;
    }
}

@media (min-width: 768px) and (max-width: 991.98px) and (min-height: 575.98px) {
    .areas__container {
        justify-content: space-around;
    }
    .col-12 {
        flex: 0 0 auto;
    }
    .col {
        flex: 0 0 auto;
    }
}