.action-page {
    height: 100vh;
    display: flex;
    flex-direction: column;
    padding-left: 2rem;
    padding-right: 2rem;
    background-color: #f0f0f0;
}

.action-page__receipt {
    background-color: white;
    border: 0;
    border-radius: 2rem;
}

.action-page__receipt-title {
    color: #0ae;
    border-bottom: #d0d0d0 6px dotted;
    padding: 1.5rem 1.5rem 1rem 1.5rem;
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.action-page__receipt-title h6{
    color: black;
}

.action-page__receipt-body__career  {
    padding: 1rem 1.5rem 0 1.5rem;
    display: flex;
    justify-content: space-between;
}
.action-page__receipt-body__F2D2  {
    padding: 0 1.5rem;
    font-size: .9rem;
}

.action-page {
    --stamp-button-size: 100%;
    --stamp-button-font: 4rem;
}
.action-page .stamp-button__text {
    font-weight: 500;
}
.action-page .stamp-button {
    padding: 9rem;
}
.action-page__receipt-footer {
    border-radius: 0 0 2rem 2rem;
    background-color: #0ae;
    color: white;
    padding: .75rem;
    cursor: pointer;
}

@media (max-width: 767.98px) {
    .action-page {
        padding-left: .75rem;
        padding-right: .75rem;
        height: auto;
    }
    .action-page__receipt-title {
        padding: 1rem 1.5rem .5rem 1.5rem;
    }
    .action-page .stamp-button {
        padding: 1.25rem;
        --stamp-button-size: 125px;
        --stamp-button-font: 1rem;
    }
    
}